import React,{useState} from 'react';
import PropTypes from 'prop-types';
import { Panel, List, Button, Group, Div, Avatar, PanelHeader,
  		Epic, Tabbar, TabbarItem, View} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon28SettingsOutline from '@vkontakte/icons/dist/28/settings_outline';
import Icon24Note from '@vkontakte/icons/dist/24/note';
import Icon28GraphOutline from '@vkontakte/icons/dist/28/graph_outline';
import Icon28WalletOutline from '@vkontakte/icons/dist/28/wallet_outline';
import Icon28ArticleOutline from '@vkontakte/icons/dist/28/article_outline';
const Home = (props) => {
	let [activeStory, setActiveStory] = useState('feed');
	let onStoryChange = (e) => {
		setActiveStory(e.currentTarget.dataset.story);
	}
	return 	<Panel id={props.id} style={{ marginTop: -50}}>
		<Epic activeStory={activeStory} tabbar={
			<Tabbar>
				<TabbarItem
					onClick={onStoryChange}
					selected={activeStory === 'feed'}
					data-story="feed"
					// text="Квесты"
				><Icon28ArticleOutline/></TabbarItem>
				<TabbarItem
					onClick={onStoryChange}
					selected={activeStory === 'messages'}
					data-story="messages"
					// text="Лидеры"
				><Icon28GraphOutline/></TabbarItem>
				<TabbarItem
					onClick={onStoryChange}
					selected={activeStory === 'discover'}
					data-story="discover"
					// text="Поиск"
				><Icon28WalletOutline/></TabbarItem>
				<TabbarItem
					onClick={onStoryChange}
					selected={activeStory === 'settings'}
					data-story="settings"
					// text="Настройки"
				><Icon28SettingsOutline/></TabbarItem>

			</Tabbar>
		}>
			<View id="feed" activePanel="feed">
				<Panel id="feed" >
					<PanelHeader>Новости</PanelHeader>
					<Group>
						Привет
					</Group>
				</Panel>
			</View>
			<View id="discover" activePanel="discover">
				<Panel id="discover">
					<PanelHeader>Поиск</PanelHeader>
				</Panel>
			</View>
			<View id="messages" activePanel="messages">
				<Panel id="messages">
					<PanelHeader>Сообщения</PanelHeader>
				</Panel>
			</View>
			<View id="notifications" activePanel="notifications">
				<Panel id="notifications">
					<PanelHeader>Уведомления</PanelHeader>
				</Panel>
			</View>
			<View id="settings" activePanel="settings">
				<Panel id="settings">
					<PanelHeader>Ещё</PanelHeader>
				</Panel>
			</View>
		</Epic>
	</Panel>
};

Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};

export default Home;
