import React from 'react';
import PropTypes from 'prop-types';
import persik from '../img/persik.png';
import './Persik.css';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import {Panel, PanelHeader, PanelHeaderButton, platform, IOS,
        Div, Button, Group, CardGrid, Card, Header} from '@vkontakte/vkui';
const osname = platform();

const Start = props => (
    <Panel id={props.id}>
        <PanelHeader>
            VK-APP
        </PanelHeader>
        <Group>
            <img style={{width: '100%', }} src="https://горныевиды.екатеринбург.рф/media/news/news_103425_image_900x_.jpg" alt=""/>
        </Group>
        <Group separator="hide">
            <CardGrid>
                <Card size="l" mode="shadow" style={{height: 200, display: 'flex', justifyContent: 'center', alignItems: 'center'}}>
                    <div style={{ display: 'flex', fontSize: 17, justifyContent: 'center', padding: 15, color: '#424242'}}>
                        Привет, {props.fetchedUser && props.fetchedUser.first_name}! Мы рады представить тебе платформу для стимулирования
                       ведения здорового образа жизни и потреблению необходимых продуктов. Тебе нужно будет выполнять разные задания и двигаться в таблице
                        LeaderBoard. Анализируй статистику, соревнуйся с друзьями, совершенствуйся!
                    </div>
                </Card>
            </CardGrid>
        </Group>
        <Group>
            <Div style={{ display: 'flex', justifyContent: 'center', }}>
                <Button size='xl' stretched style={{}} onClick={props.go} data-to='reg'>Попробовать</Button>
            </Div>
        </Group>
    </Panel>
);

Start.propTypes = {
    id: PropTypes.string.isRequired,
    go: PropTypes.func.isRequired,
    fetchedUser: PropTypes.shape({
        photo_200: PropTypes.string,
        first_name: PropTypes.string,
        last_name: PropTypes.string,
        city: PropTypes.shape({
            title: PropTypes.string,
        }),
    }),
};

export default Start;
